import { Box, Text } from "@chakra-ui/react";
import React from "react";
import "./Home.css";
import image from "../background2.png";
function Home() {
  return (
    <Box
      position="relative"
      backgroundImage={image}
      paddingTop="90px"
      height="750px"
      w="full"
      // bgColor="#F7E3CC"
      // border="2px solid red"
    >
      <Box mt="90px" p="20px" w="70%">
        <Text
          color="#fff"
          fontFamily="serif"
          // fontWeight="bold"
          fontSize={["30px", "30px", "40px"]}
        >
          Hello, I'am a
        </Text>
        <Text
          color="#fff"
          fontFamily="cursive"
          // fontWeight="bold"
          fontSize={["50px", "50px", "70px"]}
        >
          Full Stack
        </Text>
        <Text
          color="#fff"
          fontFamily="cursive"
          // fontWeight="bold"
          fontSize={["50px", "50px", "70px"]}
        >
          Web Developer
        </Text>
      </Box>
    </Box>
  );
}

export default Home;
